import { isTable, isHeadingRow, normalizeTable } from './utils';

// table cell replacement logic.
function cell(content: string, node: HTMLTableCellElement) {
  const index = Array.prototype.indexOf.call(node.parentNode?.childNodes, node);
  let prefix = ' ';
  if (index === 0) prefix = '| ';
  return prefix + content + ' |';
}

//  table cell rules.
const tableCell = {
  filter: ['th', 'td'],
  replacement: function (content: string, node: HTMLTableCellElement) {
    const cellContent = content.replace(/\n+/g, ' ');
    return cell(cellContent, node);
  },
};

//  table row rules.
const tableRow = {
  filter: 'tr',
  replacement: function (content: string, node: HTMLTableRowElement) {
    let borderCells = '';
    const alignMap = { left: ':--', right: '--:', center: ':-:' };

    if (isHeadingRow(node)) {
      for (let i = 0; i < node.children.length; i++) {
        let border = '---';
        const align = (
          node.children[i].getAttribute('align') || ''
        ).toLowerCase();

        if (align) border = alignMap[align] || border;

        borderCells += cell(border, node.children[i] as HTMLTableCellElement);
      }
    }
    return '\n' + content + (borderCells ? '\n' + borderCells : '');
  },
};

//  table rules.
const table = {
  filter: function (node: HTMLElement) {
    if (!isTable(node)) {
      return false;
    }

    if (isHeadingRow(node.rows[0])) {
      normalizeTable(node);
      return false;
    }

    return true;
  },

  replacement: function (content: string) {
    content = content.replace('\n\n', '\n');
    return content;
  },
};

//  table selection rules.
const tableSection = {
  filter: ['thead', 'tbody', 'tfoot'],
  replacement: function (content?: string) {
    return content;
  },
};

export default {
  tableCell,
  tableRow,
  table,
  tableSection,
};
